import React from 'react';
import { connect } from 'react-redux';
import jsPDF from 'jspdf';
import PDF, { Text, Image } from 'jspdf-react';
import Modal from 'react-bootstrap/Modal';
import {
	visibleLoaderAction
} from '../../../actions/settingsAction';
import moment from 'moment';



const styleTitulo = {
    fontSize: '1rem',
    color: 'black'
}

const properties = { header: 'Acme'}

let head = [    
    ["Clave","Asignatura", "Créditos","Cal.Número","Cal.Letra", "Ciclo Escolar","Grupo","Ord/Ext"],
];

const options = { year: 'numeric', month: 'long', day: 'numeric' };
let headDatos = [    
    ["","", "Fecha:",(new Date().toLocaleDateString(undefined, options)).toString()],
];

const img = ""
class PDFcalificaciones extends React.Component{
    constructor(props) {
        super(props);
            this.cerrarVisorPdf = this.cerrarVisorPdf.bind(this)
            this.state = {
            abrirVisorPdf:false,  
            agente:"mi agente select",
            alumnoNombre:(this.props.dataAlumno) ? this.props.dataAlumno :"",             
            licenciatura:(this.props.licenciatura) ? this.props.licenciatura :"",             
            fechaIngreso:(this.props.dataAlumno) ? this.props.FechaIngreso :"", 
            arrayToTablePDF:(this.props.arrayTablePDF) ? this.props.arrayTablePDF :"", 
            AnoIngreso: props.ingresoAno,
            ingreso: props.ingreso,
            nombreAlumno:"",     
            aprobadas: "",
            reprobadas:"",          
            RVOE:(this.props.RVOE) ? this.props.RVOE :"", 
            DGES:(this.props.DGES) ? this.props.DGES :"", 
            Fecha:(this.props.Fecha) ? this.props.Fecha :"", 
            Creditos:(this.props.Creditos) ? this.props.Creditos :"", 
            Materias:(this.props.Materias) ? this.props.Materias :"", 
            PlanEscolar:(this.props.PlanEscolar) ? this.props.PlanEscolar :"", 
            porcentajeCreditos:(this.props.porcentajeCreditos) ? this.props.porcentajeCreditos :"", 

        }
    }

    isLoading(active) {
        if (this.props.isLoading) {
            this.props.isLoading(active);
        }
    }

    componentWillReceiveProps(props){                
        this.setState({      
            alumnoNombre:(props.dataAlumno && props.dataAlumno.Nombre) ? props.dataAlumno.Nombre :"",    
            alumnoMatricula:(props.dataAlumno && props.dataAlumno.Matricula) ? props.dataAlumno.Matricula :"", 
            fechaIngreso: (props.dataAlumno && props.dataAlumno.FechaIngreso) ? props.dataAlumno.FechaIngreso :"",
            licenciatura:(this.props.licenciatura) ? this.props.licenciatura :"",    
            ingreso:(this.props.ingreso) ? this.props.ingreso :"", 
            AnoIngreso: props.ingresoAno,
            arrayToTablePDF:(props.arrayTablePDF) ? props.arrayTablePDF: "",  
            porcentajeCreditos:(this.props.porcentajeCreditos) ? this.props.porcentajeCreditos :"", 
        });          
        if(props.descarga){                                 
            this.getPDFInfo2();             
            this.props.cambiandoEstadoDescarga(false)
        }
        this.setState({          
        });         
    }

    cerrarVisorPdf(){
        this.setState({
            abrirVisorPdf: false
        });
    }

    getPDFInfo2(){                              
        var fecha =  this.state.Fecha
        var fechaDGES = moment(fecha).format('LL');   
        moment.locale(fechaDGES);
        //var fechaDGES=fecha.toLocaleString();          
        //var fechaDGES = moment(this.state.Fecha, "DD-MM-YYYY");      
		this.setDataTable(this.state.arrayToTablePDF,fechaDGES);
    }

    setDataTable(response,fechaDGES) {        
        let objArray = [response]   
        let dataHistorial = [];
        let apro = 0;
        let repro = 0;
        let sumaCalif = 0;
        let promedio = 0;
        let numCreditos = 0;        

		if(Array.isArray(response)){
			objArray = response
        }  
            
        
		dataHistorial = objArray.reduce(function(e, objV,index) {               
            //CALCULANDO CREDITOS
            numCreditos = numCreditos + objV.credits;
            // CALCULANDO APROBADAS Y REPROBADAS 
            if(objV.Promedio >= 6){
                apro = apro + 1
                sumaCalif = sumaCalif + objV.Promedio;
            }else{
                repro = repro + 1 
            }                      
                                
            let titulo ;
            let claveAsignatura;
            let nombreAsignatura;
            let creditos;
            let calNumero;
            let calLetra;
            let cicloEscolar;
            let grupo;
            let ordExt;			
            titulo = objV.Semestre;		            
            claveAsignatura = objV.CodigoMateria;
            nombreAsignatura = objV.Materia;
            creditos = objV.credits;	
            calNumero = (objV.Promedio > 5) ? objV.Promedio : "5";
            calLetra = (objV.Promedio > 5) ? objV.Letra : "Cinco" ;
            cicloEscolar = objV.Ciclo;
            grupo = objV.Grupo;
            ordExt = objV.Ordinario;	

            /*titulo = (<tr>
				<td bgcolor="grey" colspan="10" className="text-center">{objV.Semestre}</td>															
			</tr>)
			*/
            
            let renglonTitulo = titulo;

            if(index === 0){
                dataHistorial.concat() 

                e.push(
                    [
                        "",
                        renglonTitulo,
                        "",
                        "",
                        "",
                        "",
                        "",
                        ""
                    ]
                    ,
                    [
                    (claveAsignatura === "") ? claveAsignatura = "-": claveAsignatura,
                    nombreAsignatura,
                    creditos,
                    calNumero,
                    calLetra,
                    cicloEscolar,
                    grupo,
                    ordExt
                ])

                return e;
			}else {
				if(objArray[index - 1]["Semestre"] === objV.Semestre){
                    e.push([
                        (claveAsignatura === "") ? claveAsignatura = "-": claveAsignatura,
                        nombreAsignatura,
                        creditos,
                        calNumero,
                        calLetra,
                        cicloEscolar,
                        grupo,
                        ordExt
                    ])

					return e;
				}else {  
                    e.push(
                        [
                            "",
                            renglonTitulo,
                            "",
                            "",
                            "",
                            "",
                            "",
                            ""
                        ]
                        ,[
                        (claveAsignatura === "") ? claveAsignatura = "-": claveAsignatura,
                        nombreAsignatura,
                        creditos,
                        calNumero,
                        calLetra,
                        cicloEscolar,
                        grupo,
                        ordExt
                    ])

					return e;
				}
            }                  		
        }, [] );

        
        let aproReal = apro.toString();
        let reproReal = repro.toString();
 
        // CALCULANDO PROMEDIO                    
        promedio = sumaCalif/apro;
        let promedioFormat = (Math.round(promedio * 100) / 100).toFixed(2);
        let promedioReal = promedioFormat.toString();  
        
        this.props.visibleLoaderAction(false);	    
        let numCreditosTwoDecimals = numCreditos.toFixed(2);
        this.download(dataHistorial,aproReal,reproReal,promedioReal,numCreditosTwoDecimals,fechaDGES);
       
    }

    download(dataHistorial,aproReal,reproReal,promedioReal,numCreditosTwoDecimals,fechaDGES){ 
        var doc = new jsPDF();     
        //const options = { year: 'numeric', month: 'long', day: 'numeric' };
       // let date = (new Date().toLocaleDateString(undefined, options)).toString();        
        var pageWidth = doc.internal.pageSize.width || doc.internal.pageSize.getWidth(); 
        //var titles_R_Y = 90; 
        //var titles_R_X = 15;
        //var titles_L_Y = 90;
        //var titles_L_X = 125;  
        //var fontSize = 11;        
        doc.setFont('Arial');
        doc.setFontType('bold');
        doc.setFontSize(9);
        doc.setTextColor(10, 86, 135);
        doc.text('Universidad Westhill',pageWidth/2,10, 'center'); 
        
        doc.addImage(img,'JPEG',(pageWidth - 20) / 2,12,20,10);

        doc.setFont('Arial');
        doc.setFontType('bold');
        doc.setFontSize(7);      
        doc.setTextColor(0,0,0);   
        doc.text('HISTORIA ACADÉMICA',pageWidth/2,25, 'center');       
        
        doc.line(10, 30, 200, 30) 

        doc.setFont('Arial');
        doc.setFontType('bold');
        doc.setFontSize(7);
        doc.setTextColor(18, 86, 135);
        var licenciatura = this.state.licenciatura.replace("UW","")        

        doc.text(licenciatura,pageWidth/2,34, 'center');           
        
        doc.setFont('Arial');
        doc.setFontType('bold');
        doc.setFontSize(7);
        doc.setTextColor(0,0,0);   
        var textRVOE = 'RVOE: ' + this.state.RVOE +'    FECHA: ' + fechaDGES +'    DGES: ' + this.state.DGES
        doc.text(textRVOE ,pageWidth/2,39, 'center'); 
        
        doc.line(10, 41, 200, 41) 

        var dataDatos = [ 
                            ["Nombre", this.state.alumnoNombre, "Matrícula", this.state.alumnoMatricula],
                            ["Ingreso",this.state.ingreso, "Créditos", numCreditosTwoDecimals  + " DE " + this.state.Creditos],
                            ["Aprobadas",aproReal + " DE " + this.state.Materias, "Avance Créditos", (this.state.porcentajeCreditos ==='Infinity')?"No calculado": this.state.porcentajeCreditos.toFixed(2) + "%"],
                            ["Reprobadas",reproReal, "Promedio", promedioReal],
                        ];
            
        doc.setFontSize(8);

        doc.autoTable({
            head: headDatos,
            body:dataDatos,
            theme:'plain',                
            margin:{
                right: 10,
                left: 10,
                top:5,
                bottom:0,
            },
            startY: 43,
            columnStyles: {
                0: {cellWidth: 20},
                1: {cellWidth: 120},
                2: {cellWidth: 20},
                3: {cellWidth: 50},
            },
            styles: {cellPadding: 1,fontSize: 6},            
            didParseCell: function (table,body) {
                if (table.section === 'head') {
                    table.cell.styles.fillColor = '#FFF';
                    table.cell.styles.padding = 0;
                    table.cell.styles.textColor = '#000';       
                    table.cell.styles.align = 'left';  
                    table.cell.styles.height = '0';     
                }         
  
                if(table.section === 'body'){
                    table.cell.styles.align = 'left';
                    table.cell.styles.border = '0'; 
                }
                
            },
            })


        doc.autoTable({
        head: head,
        body:dataHistorial,
        theme:'striped',                
        margin:{
            right: 10,
            left: 10,
            top:5,
            bottom:0,
        },
        startY: 70,
        columnStyles: {
            0: {cellWidth: 15},
            1: {cellWidth: 70},
            2: {cellWidth: 'auto'},
            3: {cellWidth: 'auto'},
            4: {cellWidth: 'auto'},
            5: {cellWidth: 'auto'},
            6: {cellWidth: 'auto'},
            7: {cellWidth: 'auto'},
        },
        styles: {cellPadding: 0.5,fontSize: 6},            
        didParseCell: function (table,body) {
            if (table.section === 'head') {
                table.cell.styles.fillColor = '#125687';
                table.cell.styles.padding = 2;
                table.cell.styles.textColor = '#FFF';       
                table.cell.styles.halign = 'center';      
            }
            if (table.row.raw[0] === '') {
                table.cell.styles.fillColor = '#125687';
                table.cell.styles.padding = 2;
                table.cell.styles.textColor = '#FFF';    
                table.cell.styles.lineWidth = '0';                    
                table.cell.styles.halign = 'left';
            }    
            
            if (table.row.raw[0] === 'Clave') {
                
            }    


            if(table.column.raw === "1" && table.section === 'body'){
                table.cell.styles.halign = 'left';
                table.cell.styles.padding = 0;
                table.cell.styles.magin = 0;
            }else {
                table.cell.styles.halign = 'center';
                table.cell.styles.padding = 0;
                table.cell.styles.magin = 0;
            }
            
        },
        
        
        })
        
        /*doc.text("Dirección: " + this.state.direccion,16,72,styleEncabezados)
        doc.text("Colonia: " + this.state.colonia,16,82,styleEncabezados)
        doc.text("Municipio: " + this.state.municipio,75,82,styleEncabezados)
        doc.text("CP: " + this.state.codigoPostal,143,82,styleEncabezados)*/
        doc.setFontSize(8);                
        doc.setFontSize(8);
        //doc.output('datauri');       
        let nameFile = (this.props.dataAlumno.Nombre) ? this.props.dataAlumno.Nombre :"historial"
        doc.output('save', nameFile.replace(/\s+/g, '_')+'.pdf');      
        
    }

     

    render(){
        return(
    <React.Fragment>
        <div className="container-fluid visorPdf">
            <Modal
                size="sm"
                centered
                dialogClassName="modal-90w"
                show={this.state.abrirVisorPdf}
                onHide={this.cerrarVisorPdf.bind(this) }>
                <Modal.Header closeButton className="botonCerrar">
                    <Modal.Title >visorPdf</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <PDF
                        properties={properties}
                        preview={true}>
                            <Text x={5} y={20} style={styleTitulo} >UNIVERSIDAD WESTHILL</Text>
                            <Image src={img}  x={140} y={8} width={60} height={20}  />                                                                                                         
                    </PDF>
                </Modal.Body>
            </Modal>                        
        </div>
    </React.Fragment>
        );
    }
}
const mapStateToProps = state => ({
	fixNavbar: state.settings.isFixNavbar,
	statisticsOpen: state.settings.isStatistics,
	statisticsClose: state.settings.isStatisticsClose,
})

const mapDispatchToProps = dispatch => ({
	visibleLoaderAction: (e) => dispatch(visibleLoaderAction(e))
})
export default connect(mapStateToProps, mapDispatchToProps)(PDFcalificaciones);